import React from 'react';
//import FlipCard from 'react-flipcard';
import ReactCardFlip from 'react-card-flip';
import Screensshot_1 from './flipcards/wingitfaceDec - transparent_1.png';

class FlippingCards extends React.Component {
  constructor() {
    super();
      this.state = {
      isFlipped: false
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    e.preventDefault();
    this.setState(prevState => ({ isFlipped: !prevState.isFlipped }));
  }

  render() {
    return (
        <ReactCardFlip
        isFlipped={this.state.isFlipped}
      >
        <front >
            <div className='ba b--black br3 pa3 ma2 bw0.1 grow shadow-5 tc near-black pointer' onMouseUp={this.handleClick}>
            <h2>{'Face Recognition App'}</h2>
            <img        width="100%"
                        alt="screenshot"
                        src={Screensshot_1}
                    />
            <p>Developed a full stack web application with React, Express, PostgreSQL to demonstrate the power of artificial intelligence to users using the Clarifai Face Detection API.</p>
            
            <br/>
            <a class="f6 link dim ba ph3 pv2 mb2 dib dark-gray" href="#0">Click to View</a>
            </div>
        </front>

        <back>
            <div class="bg-purple ba b--gold br3 pa3 ma2 bw0.5 shadow-1 tc yellow" onMouseLeave={this.handleClick}>
            <h2>{'Face Recognition App'}</h2>
            <img        width="100%"
                        
                        alt="screenshot"
                        src={Screensshot_1}
                    />
            <p>Successfully implemented a secure REST API with express using authenticated routes and user- authentication.

Securely stores user information by hashing passwords in a PostgreSQL database and uses Redis to achieve efficient session management and caching.</p>
            <br/>
            <a class="f6 link dim ba ph3 pv2 mb2 dib yellow" href="#0">Click to View</a>
            </div>
        </back>
        
      </ReactCardFlip>
      
    )
  }
}

export default FlippingCards;