import React from 'react';
//import FlipCard from 'react-flipcard';
import ReactCardFlip from 'react-card-flip';
import Screensshot_1 from './wingitBeztech-transparent.png';

class FlippingCards5 extends React.Component {
  constructor() {
    super();
      this.state = {
      isFlipped: false
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    e.preventDefault();
    this.setState(prevState => ({ isFlipped: !prevState.isFlipped }));
  }

  render() {
    return (
        <ReactCardFlip
        isFlipped={this.state.isFlipped}
      >
        <front >
            <div className='ba b--black br3 pa3 ma2 bw0.1 grow shadow-5 tc near-black pointer' onMouseUp={this.handleClick}>
            <h2>{'Bestech Website'}</h2>
            <img        width="100%"
                        alt="screenshot"
                        src={Screensshot_1}
                    />
            <p>Originally a bootstrap website,  managed to adapt it to a Wordpress theme. Used Html, CSS, Javascript - jQuery for functionality e.g. a scroll to top button that fades depending on screen position</p>
            
            <br/>
            <a class="f6 link dim ba ph3 pv2 mb2 dib dark-gray" href="#0">Click to View</a>
            </div>
        </front>

        <back>
            <div class="bg-purple ba b--gold br3 pa3 ma2 bw0.5 tc shadow-1 yellow" onMouseLeave={this.handleClick}>
            <h2>{'Bestech Website'}</h2>
            <img        width="100%"
                        
                        alt="screenshot"
                        src={Screensshot_1}
                    />
            <p>Wordpress The first version of this portfolio site, featuring CSS grid for layout. JavaScript and jQuery for functionality e.g. a scroll to top button that fades depending on screen position</p>
            <br/>
            <a class="f6 link dim ba ph3 pv2 mb2 dib yellow" href="#0">Click to View</a>
            </div>
        </back>
        
      </ReactCardFlip>
      
    )
  }
}

export default FlippingCards5;