import React from 'react';
//import FlipCard from 'react-flipcard';
import ReactCardFlip from 'react-card-flip';
import './Flipcards.css';
import Screensshot_1 from './wingitfaceDec-tecfinder.png';

class FlippingCards2 extends React.Component {
  constructor() {
    super();
      this.state = {
      isFlipped: false
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    e.preventDefault();
    this.setState(prevState => ({ isFlipped: !prevState.isFlipped }));
  }

  render() {
    return (
        <ReactCardFlip
        isFlipped={this.state.isFlipped}
      >
        <front >
            <div className='ba b--black br3 pa3 ma2 bw0.1 grow shadow-5 tc near-black pointer' onMouseUp={this.handleClick}>
            <h2>{'Robo Search App'}</h2>
            <img        width="100%"
                        alt="screenshot"
                        src={Screensshot_1}
                    />
            <p>This app uses React and Redux to manage the users and the search ability. Using redux made it a lot easier to manage the app data and make it scalable in the future.</p>
            
            <br/>
            <a class="f6 link dim ba ph3 pv2 mb2 dib dark-gray" href="#0">Click to View</a>
            </div>
        </front>

        <back>
            <div class="bg-purple ba b--gold br3 pa3 ma2 bw0.5 shadow-1 tc yellow" onMouseLeave={this.handleClick}>
            <h2>{'Robo Search App'}</h2>
            <img        width="100%"
                        
                        alt="screenshot"
                        src={Screensshot_1}
                    />
            <p>Using redux made it a lot easier to manage the app data and make it scalable in the future, with the CSS framework ‘Tachyons’ to implement a clean and responsive design.</p>
            <br/>
            <a class="f6 link dim ba ph3 pv2 mb2 dib yellow" href="#0">Click to View</a>
            </div>
        </back>
        
      </ReactCardFlip>
      
    )
  }
}

export default FlippingCards2;