import React from 'react';
//import FlipCard from 'react-flipcard';
import ReactCardFlip from 'react-card-flip';
//import Screensshot_1 from './wingitfaceDec-moneyMu.png';
import Screensshot_1 from './wingittasks-transparent.png';

class FlippingCards3 extends React.Component {
  constructor() {
    super();
      this.state = {
      isFlipped: false
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    e.preventDefault();
    this.setState(prevState => ({ isFlipped: !prevState.isFlipped }));
  }

  render() {
    return (
        <ReactCardFlip
        isFlipped={this.state.isFlipped}
      >
        <front >
            <div className='ba b--black br3 pa3 ma2 bw0.1 grow shadow-5 tc near-black pointer' onMouseUp={this.handleClick}>
            <h2>{'Task Manager App'}</h2>
            <img        width="100%"
                        alt="screenshot"
                        src={Screensshot_1}
                    />
            <p>A progressive web app to manage tasks - JavaScript framework using Components, Styling via CSS and Event Handlers generating code to manipulate the DOM, which gives better client run-time performance.</p>
            
            <br/>
            <a class="f6 link dim ba ph3 pv2 mb2 dib dark-gray" href="#0">Click to View</a>
            </div>
        </front>

        <back>
            <div class="bg-purple ba b--gold br3 pa3 ma2 bw0.5 shadow-1 tc yellow" onMouseLeave={this.handleClick}>
            <h2>{'Contact Manager App'}</h2>
            <img        width="100%"
                        
                        alt="screenshot"
                        src={Screensshot_1}
                    />
            <p>A progressive web app to manage tasks - JavaScript framework using Components, Styling via CSS and Event Handlers generating code to manipulate the DOM, which gives better client run-time performance.</p>
            <br/>
            <a class="f6 link dim ba ph3 pv2 mb2 dib yellow" href="#0">Click to View</a>
            </div>
        </back>
        
      </ReactCardFlip>
      
    )
  }
}

export default FlippingCards3;